/** Variables **/
/* stylelint-disable-next-line value-keyword-case */
/** General **/
:root {
  --accent-color: #4285f4 !important;
  --accent-color-hover: #3b5074 !important;
  --accent-color-active: #3daee9 !important;
  --accent-rgb: rgba(66, 133, 244, 0.3) !important ;
}

/** Fonts **/
* {
  font-family: ubuntu, sans-serif !important;
}

.markup-2BOw-j pre,
.markup-2BOw-j pre *,
.markup-2BOw-j pre code,
.markup-2BOw-j pre code *,
.markup-2BOw-j code.inline {
  font-family: "Operator Mono", monospace !important;
}

.ace_editor::before {
  font-family: "Operator Mono", monospace !important;
}